.error-main {
  display: grid;
  place-items: center;
  height: calc(100dvh - 9rem);
}
.error-ctn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.error-ctn h1 {
  font-size: 6rem;
  color: var(--primaryColor);
}
.error-ctn h2 {
  color: #ff4857;
}
